<template>
  <div class="wrap">
    <yn-form :model="model" :rule="rules" ref="form">
      <yn-form-item label="" prop="name">
        <yn-input v-model="model.name"></yn-input>
      </yn-form-item>
      <yn-form-item label="" prop="password">
        <yn-input v-model="model.password"></yn-input>
      </yn-form-item >
      <yn-form-item label="" prop="company">
        <select name="" id="" v-model="model.company">
          <option value=""></option>
          <option value="0"></option>
          <option value="1"></option>
          <option value="2"></option>
          <option value="3"></option>

        </select>
      </yn-form-item >
      <yn-form-item label="" prop="checked">
          <yn-checkbox v-model="checked" @change="handleClick"></yn-checkbox>
          <span>{{checked }}</span>
      </yn-form-item>
      <yn-form-item>
        <button @click="submit"></button>
      </yn-form-item>
    </yn-form>
  </div>
</template>

<script>
export default {
  name: "YnFormPage",
  data() {
    return {
      checked:false,
      model:{
        name:"",
        password:'',
        checked:'',
        company:''
      },
      rules:{
        name:[
          {required:true,message:"name",trigger:'blur'},//mix,max,pattern,required,message,trigger
          {min:5,message:"name5",trigger:'change'}
        ],
        checked:[
          {required:true,message:"checked",trigger:'change'}
        ],
        company:[
          {required:true,message:"company"}
        ]
      }
    }
  },
  methods:{
    handleClick(e){
      console.log(e)
      if(e){
        this.$set(this.model,'checked',e)
      }else{
        this.$set(this.model,'checked',"")
      }
    },
    submit(){
        this.$refs.form.validate((valid)=>{
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        })
    }
  }
}
</script>

<style scoped>
    .wrap{
      width: 500px;
    }
</style>