<template>
  <div>
    <h2>yn-cascader</h2>
    <yn-cascader
      :categories="leaveOne"
      v-on:handleClick="handleClick"
    ></yn-cascader>
    <yn-cascader
      :categories="leaveTwo"
      :loading="levelSLoading"
      v-on:handleClick="handleClick"
    ></yn-cascader>
    <yn-cascader
      :categories="leaveThree"
      :loading="levelTLoading"
      v-on:handleClick="handleClick"
    ></yn-cascader>
  </div>
</template>
<script>
export default {
  name: "YnCascaderPage",
  data() {
    return {
      leaveOne: [
        { name: "", catId: 1, level: 1 },
        { name: "", catId: 2, level: 1 },
        { name: "", catId: 3, level: 1 },
        { name: "", catId: 4, level: 1 },
        { name: "", catId: 5, level: 1, selected: true },
        { name: "", catId: 6, level: 1},
        { name: "", catId: 7, level: 1 },
        { name: "", catId: 8, level: 1 },
        { name: "", catId: 9, level: 1 },
        { name: "", catId: 10, level: 1 },
        { name: "", catId: 11, level: 1 },
        { name: "", catId: 12, level: 1 },
        { name: "", catId: 13, level: 1 },
        { name: "", catId: 14, level: 1 },
      ],
      leaveTwo: [
        { name: "-20", catId: 1, level: 1 },
        { name: "-21", catId: 1, level: 1 },
        { name: "-22", catId: 1, level: 1 },
        { name: "-23", catId: 1, level: 1 },
        { name: "-24", catId: 1, level: 1 },
        { name: "-25", catId: 1, level: 1 },
        { name: "-26", catId: 1, level: 1 },
        { name: "-27", catId: 1, level: 1 },
        { name: "-28", catId: 1, level: 1 },
        { name: "-29", catId: 1, level: 1 },
      ],
      leaveThree: [],
      levelSLoading: false,
      levelTLoading: false,
    };
  },
  methods: {
    handleClick(e) {
      if (e.level === 1 || e.level === 2) {
        this.getNewList(e);
      }
      console.log(e);
    },
    getNewList(catInfo) {
      const list = []; 
      for (let i = 0; i < 10; i++) {
        const newCat = {};
        newCat.level = catInfo.level + 1
        newCat.name = catInfo.name + '-' + newCat.level + '' + i;
        newCat.catId = catInfo.catId + '-'  + i;
        list.push(newCat);
      }
      if (catInfo.level === 1) {
        this.levelSLoading = true;
        setTimeout(() => {
          this.leaveTwo = [ ...list ];
          this.leaveThree = [];
          this.levelSLoading = false;
        }, 2000);
      } else {
        this.levelTLoading = true;
        setTimeout(() => {
          this.leaveThree = [ ...list ];
          this.levelTLoading = false;
        }, 2000);
      }
    }
  }
};
</script>
