<template>
  <div>
    <h2>yn-dialog</h2>
    <ul>
      <li @click="alert">Async alert</li>
      <li @click="syncAlert">Sync alert</li>
      <li @click="confirm">Sync cancel, Async confirm</li>
      <li @click="confirm2">Async cancel, Async confirm</li>
      <li @click="confirm3">html</li>
      <li>
        <span @click="advanced">advanced</span>
        <yn-dialog v-model="show" closeModelOnClick>
          <img src="https://img.yzcdn.cn/vant/apple-3.jpg" />
        </yn-dialog>
      </li>
      <li>
        <span @click="advanced2">advanced modal click callback</span>
        <yn-dialog
          :value="this.show2"
          @buttonClick="this.handleInput"
          @modalClick="this.handleClick"
        >
          <img src="https://img.yzcdn.cn/vant/apple-3.jpg" />
        </yn-dialog>
      </li>
      <li>
        <span @click="advanced3">-{{ text.value }}</span>
        <yn-dialog v-model="show3" closeModelOnClick :beforeClose="handleBeforeClose">
          <span>{{ text.value }}</span>
        </yn-dialog>
      </li>
      <li @click="infinity"></li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "YnDialogPage",
  data() {
    return {
      show: false,
      show2: false,
      show3: false,
      text: {
        value: ""
      }
    };
  },
  methods: {
    handleAfterClose() {
      this.Toast("");
    },
    advanced2() {
      this.show2 = true;
    },
    advanced() {
      this.show = true;
    },
    advanced3() {
      this.show3 = true;
      setTimeout(() => {
        // this.text = "";
        this.text = { value: "" };
      }, 3000);
    },
    handleInput(e) {
      console.log("", e);
    },
    handleClick() {
      console.log("");
    },
    handleBeforeClose(e) {
      console.log("handleBeforeClose", e);
      if (e === "cancel") {
        return false;
      }
      return new Promise((resolve, reject) => {
        console.log(reject);
        setTimeout(() => {
          console.log("");
          resolve("");
        }, 2000);
      }).catch(err => {
        console.log("", err);
      });
    },
    handleBeforeClose2(e) {
      console.log("", e);
      return new Promise((resolve, reject) => {
        console.log(reject);
        setTimeout(() => {
          resolve("");
        }, 3000);
      }).catch(err => {
        console.log("", err);
      });
    },
    alert() {
      this.Dialog.alert({
        title: "",
        message: "",
        confirmButtonText: "",
        confirmLoadingText: "",
        beforeClose: this.handleBeforeClose,
        // afterClose: this.handleAfterClose
      });
    },
    syncAlert() {
      this.Dialog.alert({ title: "", message: "" });
    },
    confirm() {
      this.Dialog.confirm({
        title: "",
        message: "",
        beforeClose: this.handleBeforeClose
      });
    },
    confirm2() {
      this.Dialog.confirm({
        title: "",
        message: "",
        beforeClose: this.handleBeforeClose2
      });
    },
    confirm3() {
      this.Dialog.confirm({
        title: "",
        message: "<span>span</span><br/>",
        beforeClose: this.handleBeforeClose2
      });
    },
    infinity(e, i = 1) {
      this.Dialog.confirm({
        title: "",
        message: `${i}`,
        afterClose: (e) => {
          if (e === "confirm") {
            this.infinity(e, i + 1);
          }
        }
      });
    }
  }
};
</script>
